$(function() {
    const $peculiaritiesList = $('.js-peculiarities-list');

    const params = {
        slidesToShow: 4,
        prevArrow: $('.js-peculiarities-prev'),
        nextArrow: $('.js-peculiarities-next'),
        adaptiveHeight: true,
        responsive: [
            {
              breakpoint: 1281,
              settings: {
                slidesToShow: 3,
              },
            },
            {
                breakpoint: 1000,
                settings: {
                    slidesToShow: 2,
                },
            },

            {
              breakpoint: 400,
              settings: {
                  slidesToShow: 1,
              },
          },
    ]
    }
    $peculiaritiesList.slick(params)
 })