$(function() {
    const $window = $(window);
    const $flexMenu = $('.js-flexmenu');
    const $flexMenuContainer = $('.js-flexmenu__container');
    const $menuItems =  $flexMenu.find('.js-menu-item');
    const fixedItems =  $menuItems.length;
    const clonedView = []
    const clonedMore = []
    let newSubMenu = null

    const width = $window.width();
    if(width > 1039 && fixedItems > 6) flexibleSetting(5, 4)

    $window.on('resize', function() {
        const width = $window.width();
        if(width > 1039 && fixedItems > 6) flexibleSetting(5, 4)
    })

    $('body').on('click', '.js-menu-btn-more', function() {
        newSubMenu.fadeToggle();
    })
    
    $(window).on('click', function(e) {
		if (!$('.js-menu-btn-more').is(e.target) && $('.js-menu-btn-more').has(e.target).length === 0 
            && !$('.js-more-menu').is(e.target) && $('.js-more-menu').has(e.target).length === 0) {
			if(newSubMenu) newSubMenu.fadeOut();
        }
	})

    function flexibleSetting(viewItem, addMoreItems) {
        for(let i = 0; i < fixedItems ; i++) {
            if(i < viewItem) clonedView.push($menuItems[i])
            if(i > addMoreItems) clonedMore.push($menuItems[i])
        }
    
        const buttonMore = `
        <button class="menu__more-btn js-menu-item js-menu-btn-more">Ещё...</button>
        `
        newSubMenu = $('<ul class="menu__more-list js-more-menu"></ul>')
        newSubMenu.html(clonedMore)
    
        let template = $('<div class="menu__item menu__more"></div>')
        
        template.append(buttonMore)
        template.append(newSubMenu)
    
        $flexMenuContainer.html(clonedView)
        $flexMenuContainer.append(template)
    }
})