$(function() {
    const $jsHistoryTop = $('.js-history-top');
    const $jsHistoryYear = $('.js-history-year');

    const params = {
        // infinite: true,
        slidesToShow: 2,
        touchMove: false,
        swipe: false,
        draggable: false,
        // arrows: false,
    }

    


    $jsHistoryTop.slick({
        ...params,
        asNavFor: ".js-history-year",
        // variableWidth: true,
        
        responsive: [
            {
              breakpoint: 1024,
              settings: {
                slidesToShow: 1,
                slidesToScroll: 1,
              },
            },
            {
                breakpoint: 768,
                settings: {
                    adaptiveHeight: true,
                    slidesToShow: 1,
                    infinite: true,
                    variableWidth: false,
                    asNavFor: ".js-history-year-mob"
                }
            }
        ]
    })

   
    $jsHistoryYear.slick({
        ...params,
        variableWidth: true,
        asNavFor: ".js-history-top",
        prevArrow: $('.js-history-prev'),
        nextArrow: $('.js-history-next'),
        responsive: [
            {
              breakpoint: 1024,
              settings: {
                slidesToShow: 1,
                variableWidth: false,
              },
            },
            {
                breakpoint: 768,
                settings: {
                   
                    slidesToShow: 3,
                    variableWidth: true,
                },
            },
        ]
    })

    $('.js-history-year-mob').slick({
      slidesToShow: 2,
      focusOnSelect: true,
      asNavFor: ".js-history-top",
      prevArrow: $('.js-mhistory-prev'),
      nextArrow: $('.js-mhistory-next'),
      responsive: [
          {
            breakpoint: 768,
            settings: {
              slidesToShow: 3,
            },
          },
          {
              breakpoint: 450,
              settings: {
                slidesToShow: 2,
              },
          },
      ]
  })
})